<template>
  <div>
    <form
      v-if="noName && (!submitted || error)"
      class="has-text-centered"
      @submit.prevent="submit"
    >
      <p
        for="name-input"
        class="subtitle"
      >
        <label for="name-input">
          One last thing <br> We'll need your first name to confirm your subscription.
        </label>
      </p>
      <div class="field is-grouped is-grouped-centered">
        <div class="control">
          <input
            id="name-input"
            v-model.trim="name"
            class="input is-primary"
            placeholder="First name"
            required="true"
          >
        </div>
        <div class="control">
          <button
            type="submit"
            class="button is-primary"
          >
            Confirm
          </button>
        </div>
      </div>
    </form>

    <ConfirmSuccess
      v-if="success"
      class="content"
    >
      {{ name }}
    </ConfirmSuccess>
    <template v-else-if="error">
      <div class="notification is-warning has-text-centered">
        Something went wrong. Please try to confirm later.
        <br>
        ({{ error }})
      </div>
    </template>
    <div
      v-else-if="submitted"
      class="has-text-centered"
    >
      <div class="loading-spinner" />
    </div>
  </div>
</template>

<script>
import confirmMd from '../../content/newsletter/confirm-success.md'
import { apiRequest, getMdVue } from '../../utils'

const ConfirmSuccess = getMdVue(confirmMd)

export default {
  components: { ConfirmSuccess },
  props: {
    noName: { type: null, default: false },
    token: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      name: '',
      submitted: false,
      success: false,
      error: false,
      endpoint: 'confirm'
    }
  },
  mounted () {
    if (!this.noName) {
      this.submit()
    }
  },
  methods: {
    async submit () {
      const token = this.token

      this.error = false
      this.submitted = true

      try {
        const response = await apiRequest(`newsletter/${this.endpoint}?token=${token}&name=${this.name}`)

        this.success = response.success
        this.name = response.name
        this.error = response.error
      } catch (error) {
        this.error = error.message
      }

      this.submitted = !this.error
    }
  }
}
</script>
